import { createElement, ensureElement } from "../../utils/utils";
import { Component } from "../base/Component";
export class Basket extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._list = ensureElement('.basket__list', this.container);
        this._total = ensureElement('.basket__price', this.container);
        this._button = ensureElement('.basket__button', this.container);
        if (this._button) {
            this._button.addEventListener('click', () => {
                events.emit('order:open');
            });
        }
    }
    set items(items) {
        if (items.length) {
            this._list.replaceChildren(...items);
            if (this._button) {
                this._button.removeAttribute('disabled');
            }
        }
        else {
            this._list.replaceChildren(createElement('p', {
                textContent: 'Корзина пуста'
            }));
            if (this._button) {
                this._button.setAttribute('disabled', 'disabled');
            }
        }
    }
    set selected(items) {
        if (this._button) {
            if (items.length) {
                this.setDisabled(this._button, false);
            }
            else {
                this.setDisabled(this._button, true);
            }
        }
    }
    set total(total) {
        this.setText(this._total, `${total} синапсов`);
    }
    render(data) {
        super.render(data);
        if (data) {
            if (data.items !== undefined) {
                this.items = data.items;
            }
            if (data.total !== undefined) {
                this.total = data.total;
            }
            if (data.selected !== undefined) {
                this.selected = data.selected;
            }
        }
        return this.container;
    }
}
