import { Api } from '../base/api';
export class LarekAPI extends Api {
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    formatImageUrl(originalUrl) {
        return this.cdn + originalUrl.replace('.svg', '.png');
    }
    async fetchProductList() {
        const response = (await this.get('/product'));
        return response.items.map((product) => (Object.assign(Object.assign({}, product), { image: this.formatImageUrl(product.image) })));
    }
    async submitOrder(orderData) {
        const result = await this.post('/order', orderData);
        return result;
    }
}
